body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.main-page {
  padding: 20px; }
  .main-page .advance-options {
    border: 1px solid black;
    margin-top: 10px;
    padding: 10px; }
  .main-page .content {
    display: -webkit-flex;
    display: flex; }
    .main-page .content .content__source, .main-page .content .content__result {
      width: 0;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      margin: 10px; }
      .main-page .content .content__source textarea, .main-page .content .content__result textarea {
        width: 100%;
        height: 1000px; }

